import { g, React } from "src/utils/view"

type Props = {
  src: string
}

export default function VideoContainer({ src }: Props) {
  return (
    <Container>
      <iframe
        width="560"
        height="315"
        src={src}
        frameBorder="0"
        allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
        allowFullScreen
      />
    </Container>
  )
}

const Container = g("div")({
  position: "relative",
  paddingBottom: "56.25%",
  paddingTop: 20,
  height: 0,
  overflow: "hidden",
  marginLeft: -3,
  marginRight: -4,
  ["iframe, object, embed"]: {
    position: "absolute",
    top: 0,
    left: 0,
    width: "100%",
    height: "100%",
  },
})
