import SiteLink from "src/components/Link"
import VideoContainer from "src/components/VideoContainer"
import { Theme } from "src/themes/light"
import { PostFrontmatter } from "src/utils/types"
import { g, React } from "src/utils/view"

type Props = {
  timeToRead: number
  frontmatter: PostFrontmatter
  index?: number
}

export function CompactPost({ index, frontmatter, timeToRead }: Props) {
  return (
    <Container index={(index || 0) % 4}>
      {frontmatter.video && <VideoContainer src={frontmatter.video} />}
      <header>
        <SiteLink
          to={frontmatter.url}
          newTab={frontmatter.type === "remark-slide"}
        >
          <Header>{frontmatter.title}</Header>
        </SiteLink>
      </header>
      <Footer>
        ({timeToRead} min read) <br />
        {frontmatter.author} -{" "}
        <time dateTime={frontmatter.date}>{frontmatter.date}</time>
        <br />
        {frontmatter.version && `v${frontmatter.version}`}
        {frontmatter.draft && <div style={{ color: "green" }}>draft</div>}
      </Footer>
    </Container>
  )
}

type ContainerProps = {
  index: number
  theme?: Theme
}

const Container = g.article<ContainerProps>`
  display: flex;
  flex-direction: column;
  box-shadow: ${({ index, theme }: ContainerProps & { theme: Theme }) =>
    theme.compact.shadows[index]};
  padding: 5,
`

const Header = g("h1")({})

const Footer = g("footer")({
  textAlign: "left",
  color: "gray",
})
